@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  font: inherit;
  color: inherit;
}

*,
::after,
::before {
  background-repeat: no-repeat;
  box-sizing: border-box;
  flex-shrink: 0;
}

:root {
  -webkit-tap-highlight-color: transparent;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  cursor: default;
  overflow-wrap: break-word;
  tab-size: 4;
  line-height: 1.5;
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

:where(html, body) {
  height: 100%;
}

:where(img, picture, video, canvas, svg) {
  display: block;
  max-width: 100%;
}

:where(ol, ul) {
  list-style: none;
}

:where(a) {
  text-decoration: none;
}

:where(button) {
  background: none;
  border: none;
  padding: 0;
  border: none;
  box-shadow: none;
  overflow: visible;
  white-space: nowrap;
  cursor: pointer;
}

:where(table) {
  border-collapse: collapse;
  border-color: currentColor;
  border-spacing: 0;
}

:where(caption, legend) {
  overflow: hidden;
  font-size: 1px;
  line-height: 0;
  color: transparent;
}

:where(iframe) {
  border-style: none;
}

/* css 주석 노출 */
.blind {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.ir {
  display: block;
  overflow: hidden;
  font-size: 1px;
  line-height: 0;
  color: transparent;
}

.screen_out {
  overflow: hidden;
  position: absolute;
  width: 0;
  height: 0;
  line-height: 0;
  text-indent: -9999px;
}

.ir_wa {
  display: block;
  overflow: hidden;
  position: relative;
  z-index: -1;
  width: 100%;
  height: 100%;
}

:root {
  --font: Dotum;
  --fonts: sans-serif;
  --font12: 12rem var(--font);
  --font13: 13rem var(--font);
  --font14: 14rem var(--font);
  --font15: 15rem var(--font);
  --font16: 16rem var(--fonts);
  --font18: 18rem var(--fonts);
  --color_font00: #fff;
  --color_font01: #1a1a1a;
  --color_font02: #5a5a5a;
  --color_line01: #1a1a1a;
  --color_bg01: #5a5a5a;
  font-family: "Noto Sans KR", sans-serif;
  font-size: 10px;
  font-weight: 400;
  color: var(--color_font02);
}

.header {
  border: 1px solid red;
}

.container:is(.main) {
  background-color: #fff;
}

.table :where(th, td) {
  border: 1px solid var(--color_line01);
  font-size: 1.1rem;
  padding: 0.5rem 1rem;
}

.table th {
  font-size: 1.2rem;
  font-weight: 500;
  color: var(--color_font00);
  background-color: var(--color_bg01);
}